import navBar from "@/components/nav-bar";
import { mapState } from 'vuex';
import utils from '../utils/method.js'
export default {
	components: {navBar},
	data() {
		return {
			order:null,
		}
	},
	computed:{
		...mapState(['hasLogin','userInfo','config'])
	},
	async onPullDownRefresh() {
		uni.showLoading({
			title:"数据加载中..."
		})
		if(this.hasLogin){
			await this.getPromoteInfo()
		}
		uni.stopPullDownRefresh();
		uni.hideLoading();
	},
	methods: {
		handleContact (e) {
			console.log(e.detail.path)
			console.log(e.detail.query)
		},
		async pay(orderId){
			 var that = this
			await this.$http({
				apiName:"wxPay",
				method:"POST",
				data:{orderId:orderId}
			}).then(res => {
				let obj = {
					nonceStr: res.nonceStr,
					timeStamp: res.timeStamp,
					package:res.packageValue,
					signType:res.signType,
					paySign:res.paySign,
					appId: res.appId,
				}
				uni.requestPayment({
				    provider: 'wxpay',
				    ...obj,
				    success: function (res) {
						uni.showToast({
							title: "支付成功",
							duration: 1500,
						})
						setTimeout(()=>{
							that.getOrder()
						},1500)
				    },
				    fail: function (err) {
				       uni.showToast({
							title: "支付失败",
							duration: 1500,
							icon:"none"
				       })
				    }
				});
			}).catch(_ => {})
			
		},
		async onGotUserInfo(e) {
			uni.showLoading({
			    title: '请求中...'
			});
			let res1 = await utils.wxLogin();
			if(res1){
				let res2 = await this.$http({
					apiName:'userLogin',
					method:'POST',
					data:res1
				})
				try{
					utils.setSesion(res2,res1);
					this.getInvite();
					this.getOrder();
				}catch(e){}
			}else{
				uni.showToast({
					icon: 'none',
					title: "授权失败",
					duration: 1500
				});
			}
			uni.hideLoading()
		 },
		 async getInvite(){
			await this.$http({
				apiName:"userInfo",
			}).then(res => {
				this.$store.commit("setmyInviteCode",res.inviteCode)
			}).catch(_=>{})
		 },
		 async getPromoteInfo(){
		 	let res = await this.$http({
		 		apiName:"getPromoteInfo",
		 	})
		 	try{
				this.order = res.Order;
		 	}catch(e){}
		 },
		 toPath(path){
			uni.navigateTo({
				url:path
			})
		 },
		 async cancelOrder(orderId){
			 let _self = this;
			 uni.showModal({
			     title: '提示',
			     content: '确定取消该订单吗',
			     success: function (res) {
			         if (res.confirm) {
			             _self.cancelOrderOk(orderId)
			         } else if (res.cancel) {
			             
			         }
			     }
			 });
			 
		 },
		 async cancelOrderOk(orderId){
			 let _self = this;
			 let res = await this.$http({
				 apiName:"cancelOrder",
				 data:{orderId:orderId},
				 method:"POST"
			 })
			 try{
			 	uni.showToast({
			 		title: "订单已取消",
			 		duration: 1500
			 	})
			 	setTimeout(_=>{
			 		_self.getOrder()
			 	},1500)				
			 }catch(e){}
		 },
		 copy(data){
		 	let _self = this
		 	uni.setClipboardData({
		 		data: data,
		 		success() {
		 			uni.showToast({
		 				title: "复制成功",
		 			})
		 		}
		 	});
		 },
		 logOut(){
			 let _self = this;
			 uni.showModal({
				 title: '提示',
				 content: '确定退出登录吗',
				 success: function (res) {
					 if (res.confirm) {
						 _self.logOutok()
					 } else if (res.cancel) {
						 
					 }
				 }
			 });
		 },
		 async logOutok(){
			 await this.$http({
				 apiName:"loginOut",
			 }).then(res => {
				 utils.rmData()
				 uni.showToast({
				 	title: "退出成功",
				 })
			 }).catch(e=>{})
		 }
	},
	async onShow() {
		uni.showLoading({
			title:"数据加载中..."
		})
		if(this.hasLogin){
			await this.getPromoteInfo()
		}
		uni.hideLoading();
	}
}
